import React, { useEffect } from "react";
import useStyles from "./useStyles";
import Grid from "@material-ui/core/Grid";
import PokeCard from "components/PokeCard";
import { connect } from "react-redux";
import { getPokeNext, getPokePrevious } from "redux/modules/pokemon";
import { getSprite } from "util/pokemon";

const PokeGrid = ({
  loading,
  stats: { leftLimit, rightLimit, currentPage },
  pokemon,
  getPokeNext,
  getPokePrevious,
}) => {
  useEffect(() => {
    getPokeNext();
  }, [getPokeNext]);

  const classes = useStyles();
  return (
    <div>
      <button disabled={loading || leftLimit} onClick={getPokePrevious}>
        Previous
      </button>
      <button disabled={loading || rightLimit} onClick={getPokeNext}>
        Next
      </button>
      <p>{currentPage}</p>
      <div className={classes.container}>
        <Grid container spacing={3}>
          {pokemon.results.map(({ name, weight, height, id, sprites }) => (
            <PokeCard
              key={name}
              name={name}
              imgUrl={getSprite(sprites)}
              weight={weight}
              height={height}
              pokeId={id}
            />
          ))}
        </Grid>
      </div>
    </div>
  );
};

const mapStateToProps = ({ pokemon, loading, error, stats }) => ({
  pokemon,
  loading,
  error,
  stats,
});

const mapDispatchToProps = (dispatch) => ({
  getPokeNext: () => dispatch(getPokeNext()),
  getPokePrevious: () => dispatch(getPokePrevious()),
});

export default connect(mapStateToProps, mapDispatchToProps)(PokeGrid);
